.slider-module {
  margin-bottom: 4rem;
}

.slider-module_container .tns-outer {
  position: relative;
  margin-top: 2rem;
}

.slider-module_container .tns-controls {
  display: none;
}

.slider-module_container .tns-controls button {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: none;
  background-color: #27AF00;
  color: #fff;
  font-weight: 900;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.5s ease;
}

.slider-module_container .tns-controls button:after {
  content: "";
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 12px;
  height: 12px;
  border-radius: 3px;
  border-top: 3px solid #fff;
  border-right: 3px solid #fff;
}

.slider-module_container .tns-controls button:first-child {
  left: -5.375rem;
}

.slider-module_container .tns-controls button:first-child::after {
  transform: rotate(-135deg) translateX(-1px);
}

.slider-module_container .tns-controls button:last-child {
  right: -5.375rem;
}

.slider-module_container .tns-controls button:last-child::after {
  transform: rotate(45deg) translateY(1.5px);
}

.slider-module_container .tns-controls button:hover {
  transform: translateY(-50%) scale(0.9);
}

.slider-module_container .tns-nav {
  display: -ms-flexbox;
  display: flex;
  gap: 0.5rem;
  margin-top: 3rem;
}

.slider-module_container .tns-nav button {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #A0A3BD;
  border: none;
  transition: width 0.5s ease, background-color 0.5s ease;
}

.slider-module_container .tns-nav button.tns-nav-active {
  width: 16px;
  background-color: #004936;
  border-radius: 4px;
  transition: width 0.5s ease, background-color 0.5s ease;
}

@media (min-width: 1024px) {
  .slider-module_container {
    max-width: 815px;
    margin: 0 auto;
  }
}
@media (min-width: 1232px) {
  .slider-module_container .tns-controls {
    display: block;
  }
}